@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,200;0,300;0,400;0,500;0,600;1,400;1,600&family=Nunito:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;1,400;1,500&family=Raleway:wght@200&display=swap");
html,
body {
  margin: 0;
  font-family: "Montserrat";
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
#root {
  position: relative;
  height: auto;
  min-height: 95vh;
}

button {
  background-color: rgb(84, 182, 137);
}
/* Sidebar */
.sidebar-container {
  background-color: rgb(84, 182, 137);
  color: white;
  margin: 0;
  align-items: center;
  text-align: center;
}
@media screen and (min-width: 800px) {
  .sidebar-container {
    width: 300px;
    position: fixed;
    height: 100vh;
    /* overflow-y: auto; */
  }
}

@media screen and (max-width: 799px) {
  .sidebar-container {
    width: auto;
    height: auto;
    position: relative;
  }
}
.sidebar-basic-info {
  border-bottom: 1px solid grey;
}
.my-photo {
  width: 230px;
  height: 230px;
  border-radius: 50%;
  padding: 1rem;
  margin-top: 1rem;
}
.side-summary {
  margin: 1rem 2rem;
}
.sidebar-social-media {
  margin: 1rem;
}

.social-icons {
  display: inline-block;
  color: black;
  background-color: white;
  padding: 0.8rem 1rem;
  margin: 0.5rem;
  border-radius: 50%;
}
/* ********************** */

/* Nav components */
.navbar-container {
  align-content: center;
  border-bottom: 1px solid grey;
  padding-bottom: 1rem;
}
.navbar-item {
  display: flex;
  flex-direction: row;
  justify-content: center;
  column-gap: 0.5rem;
  padding: 0.5rem;
  font-size: 1.2rem;
  font-weight: 600;
}

.navbar-item a {
  text-decoration: none;
  color: white;
}

.navbar-item:hover {
  color: black;
}
.navbar-item a:hover {
  color: black;
}
/* ************* */

/* About page */
/* main-container */

.main-container {
  height: 100%;
  min-height: 95vh;
}
@media screen and (min-width: 1000px) {
  .main-container,
  .footer {
    margin-left: 300px;
  }
  .main-container-child {
    display: grid;
    grid-template-columns: 2fr 2fr;
    column-gap: 0.5rem;

    height: auto;
  }
}

@media screen and (max-width: 999px) {
  .main-container,
  .footer {
    margin-left: 300px;
  }
  .main-container-child {
    display: inline-flex;
    flex-direction: column;
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 799px) {
  .main-container,
  .footer {
    margin-left: auto;
  }
  .main-container-child {
    width: auto;
    height: auto;
    position: relative;
  }
}
.skills-conatiner {
  padding-left: 4rem;
  padding-top: 2rem;
}
.skills-conatiner img {
  width: 2rem;
  height: 2rem;
}
.skills-conatiner h1 {
  font-weight: 800;
}

.skill-type h5 {
  border-left: 5px solid rgb(84, 182, 137);
  color: grey;
  padding-left: 3rem;
}
.skill-item {
  display: inline-flex;
  flex-direction: column;
  padding: 1rem;
  padding-right: 1rem;
  justify-content: center;
  text-align: center;
  align-items: center;
  font-weight: 800;
}
.skill-item span {
  padding-top: 0.2rem;
}
/* Summary component*/
.summary-container {
  padding: 3rem;
  background-color: rgb(251, 250, 252);
  color: rgb(80, 77, 77);
}
.summary-container h1,
h5,
p {
  padding: 0.5rem;
}
.summary-button {
  background-color: rgb(84, 182, 137);
  color: white;
  font-weight: 600;
}

/* project card */
.project-card {
  border: none;
  padding: 1rem;
}
.card-title {
  color: black;
}
.card-text a {
  text-decoration: none;
}
.card-text a:hover {
  color: blue;
}
.project-card img {
  height: 300px;
  width: 350px;
}
.card-body {
  display: inline-flex;
  flex-direction: row;
  column-gap: 1rem;
  justify-content: flex-start;
  align-items: center;
}
.card-footer {
  display: flex;
  column-gap: 2rem;
  align-items: center;
}

/* portfolio page */
.portfolio-heading {
  padding: 4rem 1rem 3rem 1rem;
  font-weight: 600;
  border-bottom: 1px grey;
  text-align: center;
}
/* @media screen and (min-width: 1000px) {
} */
.portfolio-container {
  display: grid;
  grid-template-columns: auto;
  max-width: fit-content;
}

/* page footer */
.footer {
  /* margin-left: 320px; */
  position: relative;
  margin-top: auto;
  bottom: 0;
  color: rgb(184, 175, 175);
  align-items: center;
}
.footer p {
  text-align: center;
}
